import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'
import Layout from 'src/components/Layout'
import { Container, Heading, Text } from 'src/components/Base'
import ReactHtmlParser from 'react-html-parser'

import styles from './legal.module.scss'

const Legal = ({ data }) => {

  const {data: page} = data.allPrismicLegalPage.edges[0].node;

  return (
    <Layout>
      <Container narrow>
        <Heading size="lg">
          {page.heading.text}
        </Heading>
        <Text className={styles.text}>
          {ReactHtmlParser(page.content.html)}
        </Text>
      </Container>
    </Layout>
  )
}


Legal.propTypes = {
  uid: PropTypes.string,
}

export const pageQuery = graphql`
  query LegalPageByUid($uid: String!) {
    allPrismicLegalPage(filter: { uid: { eq: $uid } }) {
      edges {
        node {
          data {
            heading {
              text
            }
            content {
              html
            }
          }
        }
      }
    }
  }
`

export default Legal
